$(function () {
    $('.header-nav__menu-burger').on('click', () => {
        console.log(this);
        $('.header-nav__block').toggleClass('active');
        $('.header-nav__menu-burger').toggleClass('menu-burger-active');
    });

    $('.slider-card').slick({
        arrows: false,
        infinite: false,
        responsive: [
            {
              breakpoint: 1980,
              settings: 'unslick'
              
            },
        ]
    });
});

